html {
  overflow-x: hidden;
}
body {
  max-width: 1920px;
  margin: 0 auto;
  font-family: 'CeraPro', 'Arial', 'Roboto', sans-serif;
  overflow-x: hidden;
}
a {
  outline: none;
}
a:hover {
  color: #fff;
  text-decoration: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
.modal.fade.show {
  padding-right: 0 !important;
}
.modal-open {
  padding-right: 0 !important;
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Thin.woff") format('woff');
  font-weight: 100;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Light.woff") format('woff');
  font-weight: 300;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Medium.woff") format('woff');
  font-weight: 400;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Regular.woff") format('woff');
  font-weight: 500;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Bold.woff") format('woff');
  font-weight: 700;
  font-style: 'normal';
}
@font-face {
  font-family: 'CeraPro';
  src: url("../fonts/CeraPro/CeraPro-Black.woff") format('woff');
  font-weight: 800;
  font-style: 'normal';
}
header {
  position: absolute;
  width: 100%;
  z-index: 10;
}
header .header-block {
  display: flex;
  justify-content: space-between;
}
header .header-block .block-items {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
header .logo-block {
  display: flex;
  flex-direction: column;
  background-color: #000;
  padding: 35px 100px 35px 35px;
  position: relative;
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .logo-block {
    padding: 35px 75px 35px 35px;
  }
}
@media (min-width: 1201px) and (max-width: 1425px) {
  header .logo-block {
    padding: 35px 75px 35px 35px;
  }
}
@media (min-width: 320px) and (max-width: 991px) {
  header .logo-block {
    padding: 35px;
  }
}
header .logo-block:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -454px;
  right: -800px;
  width: 800px;
  height: 455px;
  border: 20px solid #f5263f;
  z-index: 0;
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .logo-block:after {
    right: -600px;
    width: 600px;
    height: 500px;
    top: 259px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  header .logo-block:after {
    right: -400px;
    width: 400px;
    height: 418px;
    bottom: -418px;
  }
}
@media (max-width: 767px) {
  header .logo-block:after {
    height: 437px;
    bottom: -437px;
    width: 350px;
    right: -320px;
  }
}
@media (max-width: 575px) {
  header .logo-block:after {
    width: 200px;
    right: -120px;
    height: 377px;
    bottom: -377px;
  }
}
header .logo-block .logo-pre-title {
  margin-bottom: 5px;
  color: #7a7a7a;
  font-size: 12px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
}
@media (min-width: 320px) and (max-width: 991px) {
  header .logo-block .logo-pre-title {
    font-size: 8px;
  }
}
header .logo-block .logo-title {
  color: #fff;
  font-size: 18px;
  font-weight: 800;
  line-height: 1;
  text-transform: uppercase;
}
@media (min-width: 320px) and (max-width: 991px) {
  header .logo-block .logo-title {
    font-size: 12px;
  }
}
header .logo-block .logo-img {
  margin-top: 75px;
}
@media (min-width: 1201px) and (max-width: 1425px) {
  header .logo-block .logo-img {
    margin-top: 55px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .logo-block .logo-img {
    margin-top: 55px;
  }
}
@media (min-width: 320px) and (max-width: 991px) {
  header .logo-block .logo-img {
    margin-top: 50px;
  }
}
header .logo-block .logo-img .img {
  width: 125px;
  height: 125px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 1201px) and (max-width: 1425px) {
  header .logo-block .logo-img .img {
    width: 100px;
    height: 100px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .logo-block .logo-img .img {
    width: 100px;
    height: 100px;
  }
}
@media (min-width: 320px) and (max-width: 991px) {
  header .logo-block .logo-img .img {
    width: 72px;
    height: 72px;
  }
}
header .contacts-block {
  padding-top: 50px;
  display: flex;
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .contacts-block {
    display: none;
  }
}
@media (min-width: 320px) and (max-width: 991px) {
  header .contacts-block {
    display: none;
  }
}
header .contacts-block .block-item:first-child {
  margin-right: 100px;
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .contacts-block .block-item:first-child {
    margin-right: 50px;
  }
}
@media (min-width: 1201px) and (max-width: 1425px) {
  header .contacts-block .block-item:first-child {
    margin-right: 50px;
  }
}
header .contacts-block .block-item:nth-child(2) {
  margin-right: 50px;
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .contacts-block .block-item:nth-child(2) {
    margin-right: 25px;
  }
}
@media (min-width: 1201px) and (max-width: 1425px) {
  header .contacts-block .block-item:nth-child(2) {
    margin-right: 25px;
  }
}
header .contacts-block .block-item .item-title {
  margin-bottom: 5px;
  color: #fff;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
}
header .contacts-block .block-item .link a {
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  line-height: 1;
  text-transform: uppercase;
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .contacts-block .block-item .link a {
    font-size: 20px;
  }
}
@media (min-width: 1201px) and (max-width: 1425px) {
  header .contacts-block .block-item .link a {
    font-size: 20px;
  }
}
header .block-modal a {
  padding: 25px 35px;
  color: #fff;
  font-size: 16px;
  font-weight: 800;
  line-height: 1;
  text-transform: uppercase;
  background-color: #f5263f;
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .block-modal a {
    padding: 15px;
    font-size: 14px;
  }
}
@media (min-width: 1201px) and (max-width: 1425px) {
  header .block-modal a {
    padding: 15px;
    font-size: 14px;
  }
}
header .hamburger-modal {
  display: none;
  margin-top: 15px;
  width: 44px;
  height: 44px;
  background-color: #f5263f;
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .hamburger-modal {
    display: block;
  }
}
@media (min-width: 320px) and (max-width: 991px) {
  header .hamburger-modal {
    display: block;
  }
}
header .hamburger-modal i {
  display: block;
  width: 19px;
  height: 2px;
  margin-bottom: 5px;
  background-color: #fff;
  transform: translate(11px, 13px);
}
header .hamburger-modal i:last-child {
  margin-bottom: 0;
}
.preview {
  padding-top: 315px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}
@media (min-width: 1201px) and (max-width: 1425px) {
  .preview {
    padding-top: 270px;
  }
}
.preview .titles {
  padding-top: 100px;
}
@media (max-width: 575px) {
  .preview .titles {
    padding-top: 0;
  }
}
.preview .preview-title {
  color: #fff;
  font-size: 90px;
  font-weight: 800;
  line-height: 1;
  text-transform: uppercase;
  position: relative;
  z-index: 20;
}
@media (min-width: 320px) and (max-width: 991px) {
  .preview .preview-title {
    font-size: 40px;
  }
}
.preview .preview-title br:first-child {
  display: none;
}
@media (max-width: 575px) {
  .preview .preview-title br:first-child {
    display: block;
  }
}
.preview .preview-bottom-title {
  margin-top: 86px;
  display: inline-block;
  padding: 15px 40px 15px 25px;
  color: #fff;
  font-size: 48px;
  font-weight: 800;
  line-height: 1;
  text-transform: uppercase;
  background-color: #f5263f;
  position: relative;
  z-index: 20;
}
@media (min-width: 320px) and (max-width: 991px) {
  .preview .preview-bottom-title {
    font-size: 24px;
  }
}
@media (max-width: 767px) {
  .preview .preview-bottom-title {
    line-height: 0.6;
  }
}
.preview .preview-bottom-title br {
  display: none;
}
@media (max-width: 767px) {
  .preview .preview-bottom-title br {
    display: block;
  }
}
#map {
  width: 100%;
  height: 100%;
}
.modal.feedback-modal .modal-dialog {
  margin: 0 auto;
  padding: 50px 0 0 0;
  max-width: 830px;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-dialog {
    max-width: 100%;
  }
}
.modal.feedback-modal .modal-dialog .modal-content {
  padding-bottom: 50px;
  border-radius: 0;
  border: none;
}
.modal.feedback-modal .modal-dialog .modal-content .modal-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 45px 50px 45px 50px;
  border-bottom: none;
  border-radius: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-dialog .modal-content .modal-header {
    padding: 35px 25px 25px 25px;
  }
}
.modal.feedback-modal .modal-dialog .modal-content .modal-header .close {
  opacity: 1;
  padding: 0;
}
.modal.feedback-modal .modal-dialog .modal-content .modal-header .close * {
  color: #fff;
}
.modal.feedback-modal .modal-dialog .modal-content .circle {
  position: absolute;
  right: 0;
  top: 3px;
  width: 43px;
  height: 43px;
  display: inline-block;
  line-height: 39px;
  text-align: center;
}
.modal.feedback-modal .modal-dialog .modal-content .fa-times-circle {
  color: #fff;
  font-size: 50px;
}
.modal.feedback-modal .modal-dialog .modal-content .header-title {
  margin-bottom: 25px;
  color: #fff;
  font-size: 48px;
  font-weight: 900;
  text-transform: uppercase;
  line-height: 1;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-dialog .modal-content .header-title {
    font-size: 30px;
  }
}
.modal.feedback-modal .modal-dialog .modal-content .header-sub-title {
  color: #fff;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 1;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-dialog .modal-content .header-sub-title {
    font-size: 16px;
  }
}
.modal.feedback-modal .modal-body {
  padding: 0;
}
.modal.feedback-modal .modal-body-container {
  padding-top: 10px;
}
.modal.feedback-modal .modal-body-container form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 25px;
}
@media (max-width: 575px) {
  .modal.feedback-modal .modal-body-container form {
    width: 100%;
    padding: 0 10px;
  }
}
.modal.feedback-modal .modal-body-container .form-item {
  margin-bottom: 30px;
}
@media (max-width: 575px) {
  .modal.feedback-modal .modal-body-container .form-item {
    width: 100%;
  }
}
.modal.feedback-modal .modal-body-container .form-item input {
  width: 530px;
  height: 60px;
  padding: 15px;
  border: 1px solid #c6c6c6;
  color: #7d7d7d;
  outline: none;
}
@media (min-width: 576px) and (max-width: 767px) {
  .modal.feedback-modal .modal-body-container .form-item input {
    width: 400px;
    height: 60px;
  }
}
@media (max-width: 575px) {
  .modal.feedback-modal .modal-body-container .form-item input {
    width: 100%;
    height: 60px;
  }
}
.modal.feedback-modal .modal-body-container .form-item-submit input {
  width: 300px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  vertical-align: center;
  color: #fff;
  border: none;
  font-size: 18px;
  font-weight: 900;
  text-transform: uppercase;
  box-shadow: 8px 20px 35px rgba(27,79,94,0.4);
  background-image: linear-gradient(-41deg, #411e92 0%, #0074cb 100%);
  border-radius: 5px;
  transition: all 0.3s linear;
  cursor: pointer;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-body-container .form-item-submit input {
    margin-top: 0;
    width: 290px;
    height: 60px;
    line-height: 60px;
  }
}
.header-modal .modal-dialog {
  margin: 0;
  padding: 0;
  max-width: 100%;
}
.header-modal .modal-dialog .modal-content {
  padding-bottom: 100px;
  border-radius: 0;
  background-image: linear-gradient(-225deg, #761e92 0%, #0b2893 51%, #0088dc 100%);
}
.header-modal .modal-dialog .modal-content .modal-header {
  justify-content: space-between;
  align-items: center;
  padding: 25px 1rem 1rem 1rem;
  border-bottom: none;
}
.header-modal .modal-dialog .modal-content .modal-header .close {
  opacity: 1;
  padding: 0;
}
.header-modal .modal-dialog .modal-content .modal-header .close * {
  color: #fff;
}
.header-modal .modal-dialog .modal-content .circle {
  border: 1px solid $orange;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  display: inline-block;
  line-height: 38px;
  text-align: center;
}
.header-modal .modal-dialog .modal-content .logo {
  color: #fff;
  font-size: 20px;
  font-weight: 900;
  line-height: 1;
}
.header-modal .modal-dialog .modal-body-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.header-modal .modal-dialog .modal-body-container .item-title {
  margin-bottom: 30px;
}
.header-modal .modal-dialog .modal-body-container .item-title *,
.header-modal .modal-dialog .modal-body-container .item-title {
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 2.91px;
  text-align: right;
  cursor: pointer;
}
.header-modal .modal-dialog .modal-body-container .item-title *:hover,
.header-modal .modal-dialog .modal-body-container .item-title:hover {
  color: $orange;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown {
  position: relative;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown:before {
  content: url("/img/icons/triangle.png");
  position: absolute;
  right: 100px;
  top: -3px;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown ul.dropdown.show {
  display: block;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown ul.dropdown {
  display: none;
  top: 10px;
  left: 0;
  padding: 0;
  margin: 0 0 20px 0;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown ul.dropdown li {
  list-style-type: none;
  margin-bottom: 23px;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown ul.dropdown li a {
  color: $orange;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
}
